er .header-inner{padding:30px 0;box-sizing:border-box}.FloatingHeader .header-inner #siteTitleWrapper{width:255px;position:relative;left:50%;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);text-align:center}.FloatingHeader .header-inner #siteTitleWrapper h1{line-height:1em;font-family:sans-serif;font-size:2em;letter-spacing:0.0869565em;margin:0;padding-top:0;padding-bottom:0}.FloatingHeader .header-inner #siteTitleWrapper h1 a{font-weight:600;font-style:normal;font-size:23px;text-transform:uppercase;color:#fff;margin:0;padding:0;line-height:1.2rem;text-decoration:none;text-rendering:optimizeLegibility}.FloatingHeader .header-inner #headerNav{margin-left:-15px;margin-top:-28px;margin-bottom:-1px;box-sizing:border-box;display:block;-webkit-transition:all .3s linear;transition:all .3s linear;text-align:center;position:relative}.FloatingHeader .header-inner #headerNav nav{display:block;text-align:center;-webkit-font-smoothing:antialiased;line-height:1em;font-family:sans-serif}.FloatingHeader .header-inner #headerNav nav .link{display:inline-block;vertical-align:middle;margin:0;text-align:center;-webkit-font-smoothing:antialiased;line-height:1em}.FloatingHeader .header-inner #headerNav nav .link a{pointer-events:initial;color:rgba(255,255,255,0.6);font-weight:400;font-family:'Montserrat';font-style:normal;font-size:12px;letter-spacing:2px;text-decoration:none;line-height:1em;text-transform:uppercase;padding:.75em 1em;display:block;-webkit-transition:color .1s 0s ease-in-out;transition:color .1s 0s ease-in-out;outline:none}.FloatingHeader .header-inner #headerNav nav .link a.active{color:#fff !important}.SectionWrapper{padding-top:64px;padding-left:20%;padding-right:20%;padding-bottom:64px;clear:none}.SectionWrapper .SectionTitle{text-align:center;text-transform:uppercase;white-space:pre-wrap;margin-bottom:0;margin:1.5em auto .3em;margin-top:0;color:#fff;font-weight:400;line-height:3.5em;letter-spacing:2px;text-rendering:optimizeLegibility;font-size:32px